@import url(https://fonts.googleapis.com/css?family=VT323&display=swap);
.mb-0 {
  margin-bottom: 0; }

.mb-1 {
  margin-bottom: 1rem; }

.mb-2 {
  margin-bottom: 2rem; }

.mb-3 {
  margin-bottom: 4rem; }

.mb-4 {
  margin-bottom: 8rem; }

.mt-0 {
  margin-bottom: 0; }

.mt-1 {
  margin-top: 1rem; }

.mt-2 {
  margin-top: 2rem; }

.mt-3 {
  margin-top: 4rem; }

.mt-4 {
  margin-top: 8rem; }

body {
  background-color: #000000;
  color: #33FF00;
  font-family: 'VT323', monospace;
  font-size: 3rem; }

.terminal-font {
  font-family: 'VT323', monospace;
  font-size: 3rem; }

.error {
  color: #FFB000; }

.terminal-green {
  color: #33FF00; }

.center {
  text-align: center; }

.h1 {
  font-weight: normal;
  font-size: calc(5.5rem);
  text-decoration: underline; }

.h2 {
  font-weight: normal;
  font-size: 4rem;
  text-decoration: underline; }

.h3 {
  font-weight: normal;
  font-size: 3rem;
  text-decoration: underline; }

button {
  border: 2px #33FF00 solid;
  background-color: #000000;
  color: #33FF00;
  padding: 1rem;
  outline: none; }
  button:active {
    color: #FFB000;
    border: 2px #FFB000 solid; }

code {
  display: block;
  border: 2px #33FF00 solid;
  min-height: 3rem;
  padding: 1rem; }

.LineCount-lines {
  list-style: none;
  padding: 0px;
  margin: 0px; }
  .LineCount-lines .li {
    display: inline-block; }

.CodeField {
  display: flex;
  border: 2px solid #33FF00; }

.CodeField-code {
  border: none;
  outline: none;
  box-shadow: none;
  resize: none;
  border-left: 2px #33FF00 solid;
  font-size: 3rem;
  width: calc(100% - 1rem);
  background-color: #000000;
  color: #33FF00;
  padding: 1rem;
  overflow: hidden;
  flex: 1 1 95%; }

.CodeField-LineCount {
  flex: 1 1 5%;
  padding: 1rem; }

